.K7__blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.K7__blog-container_article-image {
    width: 100%;
    height: 100%;

    background: var(--color-footer);
}

.K7__blog-container_article-image img {
    width: 100%;
    height: 100%;
    opacity: 80%;
    transition: 0.5s;
}

.K7__blog-container_article-image img:hover {
    opacity: 100%;
    cursor: pointer;
}